<template>
  <header :style="{ height: `${headerHieght}px` }">
    <img class="image" src="@/assets/logo.png" alt="" />
    <div class="selected">
      <div class="selected-left">
        <div class="menu-block" :class="{'active': active === 'personal'}">
          <span @click="goLeaderCenter">个人中心</span>
        </div>
        <div class="menu-block" :class="{'active': active === 'intelligence'}">
          <NewsNav @active="val => active=val"/>
        </div>
        <div class="menu-block" :class="{'active': active === 'command'}">
          <GuideNav @active="val => active=val"/>
        </div>
      </div>
      <div class="selected-right">
        <div class="menu-block" :class="{'active': active === 'duty'}">
          <ServiceNav @active="val => active=val"/>
        </div>
        <div class="menu-block" :class="{'active': active === 'opinion'}">
          <PublicNav @active="val => active=val"/>
        </div>
        <div class="menu-block">
          <span @click="$message.warning('暂未开放')">专题应用</span>
        </div>
      </div>
    </div>
    <div class="setting">
      <img
        style="width: 25px; height: 25px; cursor: pointer"
        src="@/assets/数字提醒.png"
        alt=""
      />
      <el-dropdown>
        <span class="el-dropdown-link">
          <img
            style="width: 20px; height: 20px; cursor: pointer"
            src="@/assets/设置.png"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown" class="setting-menu">
          <el-dropdown-item
            v-for="item in settingList"
            :key="item.id"
            @click.native="handleClick(item)"
          >
            <img src="@/assets/头像小.png" alt="" />
            {{ item.name }}</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>

      <el-dropdown>
        <span class="el-dropdown-link">
          <img
            style="width: 34px; height: 34px; cursor: pointer"
            src="@/assets/头像小.png"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="item in userList"
            :key="item.id"
            @click.native="handleClick(item)"
            :disabled="item.disabled"
          >
            <img src="@/assets/头像小.png" alt="" />
            {{ item.name }}</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>

<script>
import NewsNav from "@/components/navs/newscenter/NewsNav.vue";
import GuideNav from "@/components/navs/guidecenter/GuideNav.vue";
import ServiceNav from "@/components/navs/servicecenter/ServiceNav.vue";
import PublicNav from "@/components/navs/publiccenter/PublicNav.vue";

export default {
  props: {
    headerHieght: {
      type: Number,
      default: 60,
    },
  },
  data() {
    return {
      value: "",
      active: 'personal',
      userList: [
        {
          id: 1,
          user: "UserLeader",
          name: "指挥人员",
          disabled: false,
        },
        {
          id: 2,
          user: "UserService",
          name: "勤务人员",
          disabled: false,
        },
        {
          id: 3,
          user: "UserWork",
          name: "情报人员",
          disabled: false,
        },
        {
          id: 4,
          user: "UserGuide",
          name: "舆情人员",
          disabled: false,
        },
        {
          id: 5,
          user: "Exit",
          name: "退出",
          disabled: false,
        },
      ],
      settingList: [
        {
          id: 1,
          user: "UserSetting",
          name: "个人中心配置",
        },
      ],
    };
  },
  mounted() {},
  components: {
    NewsNav,
    GuideNav,
    ServiceNav,
    PublicNav,
  },
  methods: {
    goLeaderCenter() {
      this.active = 'personal',
      this.$router.push({
        path: `/user/UserLeader`,
      });
    },
    handleClick(item) {
      if (item.user === "Exit") {
        this.$router.push("/");
        return;
      }
      if (this.$route.path.includes(item.user)) {
        return this.$message.warning("已在当前页");
      }
      this.$router.push({
        path: `/user/${item.user}`,
      });
    },
  },
};
</script>

<style scoped lang="scss">
header {
  width: 100%;
  height: 120px;
  z-index: 2;
  background: url("@/assets/Group 10 Copy.png") no-repeat;
  position: fixed;
  top: 0;
  text-align: center;
  width: 100%;
  height: 120px;
  background-position: center;
  .image {
    position: absolute;
    left: 43%;
    top: 10px;
  }
  .selected {
    width: 100%;
    height: 39px;
    margin: 34px auto 0;
    z-index: 3;

    .selected-left {
      width: 50%;
      padding: 0 214px;
      display: inline-flex;
      .menu-block{
        margin-left: 14px;
        &:first-child{
          margin-left: 0;
        }
      }
      span {
        margin-right: 25px;
      }
      ::v-deep .el-input__inner {
        text-align: center;
        background: linear-gradient(
          1deg,
          rgba(19, 135, 226, 0) 27%,
          #073f6c 100%
        );
        text-shadow: 0 0 16px #4277eb;
        padding: 0;
        font-weight: 500;
        width: 155px;
        height: 39px;
        border: none;
        color: #A4B4EA;
        border-radius: 0;
        margin-right: 25px;
        font-size: 15px;
      }
      ::v-deep .el-input__inner:hover {
        display: inline-block;
        color: #fff !important;
        background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, rgba(44, 162, 255, 0.5) 100%);
        
      }
      ::v-deep .el-input__suffix {
        display: none;
      }
      ::v-deep .el-input__inner::placeholder {
        color: #fff;
        text-align: center;
      }
    }
    .selected-right {
      width: 50%;
      padding: 0 214px;
      float: right;
      display: inline-flex;
      justify-content: flex-end;
      .menu-block{
        margin-right: 14px;
        &:last-child{
          margin-right: 0;
        }
      }
      ::v-deep .el-input__inner {
        text-align: center;
        background: linear-gradient(
          1deg,
          rgba(19, 135, 226, 0) 27%,
          #073f6c 100%
        );
        text-shadow: 0 0 16px #4277eb;
        padding: 0;
        font-weight: 500;
        width: 155px;
        height: 39px;
        border: none;
        color: #A4B4EA !important;
        border-radius: 0;
        margin-right: 25px;
        font-size: 15px;
      }
      ::v-deep .el-input__inner:hover {
        display: inline-block;
        color: #fff !important;
        background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
      }
      ::v-deep .el-input__suffix {
        display: none;
      }
      ::v-deep .el-input__inner::placeholder {
        color: #fff;
        text-align: center;
      }
    }
    .menu-block {
      width: 166px;
      height: 50px;
      border-top: 1px solid #0E4C79;
      border-bottom: 1px solid #0E4C79;
      background: #111F44;
      box-sizing: border-box;
      display: inline-block;
      padding: 5px;
      vertical-align: bottom;
      span {
        text-align: center;
        background: linear-gradient(
          1deg,
          rgba(19, 135, 226, 0) 27%,
          #073f6c 100%
        );
        text-shadow: 0 0 16px #4277eb;
        padding: 0;
        font-weight: 500;
        width: 155px;
        height: 39px;
        border: none;
        color: #A4B4EA;
        border-radius: 0;
        margin-right: 25px;
        font-size: 15px;
        display: inline-block;
        line-height: 39px;
      }
      &.hover,
      &.active{
        span{
          color: #fff;
          background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
        }
        ::v-deep .el-select{
          .el-input__inner{
            color: #fff;
            background: linear-gradient(0deg, rgba(8, 61, 104, 0) 0%, #2ca2ff 100%);
          }
        }
      }
    }
  }
  .setting {
    position: absolute;
    display: flex;
    width: 200px;
    height: 50px;
    z-index: 3;

    top: 0;
    right: 0;
    align-items: center;
    justify-content: space-around;
  }
}
.el-dropdown-menu {
  border: none;
  background: #0e2046;
}
.setting-menu {
  transform: translateX(50px);
}
::v-deep .el-dropdown-menu__item {
  display: flex;
  align-items: center;
  font-size: 12px;
  justify-content: space-between;
  color: #fff;
  img {
    width: 21px;
    margin-right: 10px;
  }
}
</style>